//  //- Đây là phần Services
.Home_Services {
    background: #17c2a4;
    min-height: 600px;
    text-align: center;
    justify-content: center;
    align-items: center;
    display: flex;
    .col h1 {
        text-transform: uppercase;
        color: #ffffff;
        font-size: 2.5rem;
        letter-spacing: 2px; }
    .col p {
        color: #ffffff;
        font-size: 1.2rem;
        width: 580px;
        height: 20px;
        margin: 40px 280px 100px; }
    .figure {
        width: 200px;
        min-height: 50px; }
    .figure img {
        margin-bottom: 50px; }
    .col-sm-3 h3 {
        color: #ffffff;
        padding-bottom: 20px;
        font-size: 1.5rem; }
    .col-sm-3 p {
        color: #ffffff; } }







